@import url(https://fonts.googleapis.com/css2?family=Nunito+Sans&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.container{width:45vw;margin:3rem auto;display:flex;align-items:center}@media screen and (max-width: 768px){.container{width:85vw;margin:1rem auto}}

h2{margin-top:3rem;border-bottom:1px solid #A7A4A9}@media screen and (max-width: 768px){h2{margin-top:2rem}}h3{font-size:1.25rem}h4{margin-bottom:12px}h5{margin-bottom:0.25rem}ul{-webkit-margin-start:-12px;margin-inline-start:-12px}

